* {
  font-family:  Helvetica, Arial, sans-serif;
}

body{
  padding: 60px;
}

input{
  color: blue;
  font-size: 1.2;
  width: 100%;
}

.card{
align-content: center;
border: 1px solid gray;
border-radius: 6px;
margin-bottom: 10px;
margin-right: 10px;
overflow: hidden;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap:wrap ;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
max-height: 170px;

}